.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 35px;
}

.item:hover {
  background-color: rgb(41, 179, 75);
  border-radius: 3px;
}

.item:hover:nth-child(2n) {
  background-color: rgb(20, 128, 230);
}

.text {
  display: inline-block;
  border: none;
  max-width: 260px;
  font-size: 18px;
  margin: 0;
  padding-left: 5px;
  text-overflow: ellipsis;
  text-align: center;
  overflow: hidden;
}

.button {
  display: inline-block;
  background-color: rgb(150, 150, 150);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 3px;
  padding: 3px 8px;
}

.button:hover {
  background-color: black;
}